.f-s {
  font-size: 12px;
}
.f-x {
  font-size: 20px;
}

.User {
  color: #555555;
  margin-top: 10px;
}
.f-b {
  font-weight: 700;
}

.line-h25 {
  line-height: 25px;
}

.white {
  color: white;
}

.h-3 {
  line-height: 30px;
  font-size: 23px;
  text-align: center;
}
.App {
  width: 100%;
  min-height: 100vh;
}

.sidebar {
  width: 350px;
  padding: 20px;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}

.card-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-height: calc(100vh - 40px);
  overflow-y: auto;
}

/* 스크롤바 스타일링 (선택사항) */
.card-container::-webkit-scrollbar {
  width: 6px;
}

.card-container::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.card-container::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 3px;
}
