.userCard {
  width: 155px;
  height: 155px;
  padding: 20px;
  background-color: #fff;
  box-sizing: border-box;
  border-radius: 15px;
  float: left;
}
.pictureCard {
  width: 155px;
  height: 155px;
  box-sizing: border-box;
  border-radius: 15px;
  float: left;
}
.Image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
}
.userCard input {
  top: 20px;
  left: 15px;
  background: transparent;
  border: none;
  font-family: "Times New Roman", Times, serif;
  font-size: 16px;
  border-bottom: 1px solid #555555;
  outline: none;
  padding: 10px;
  color: #555555;
}

.WatherCard {
  width: 330px;
  padding: 10px 20px;
  margin: 15px 0;
  background-color: rgba(14, 14, 14, 0.7);
  box-sizing: border-box;
  border-radius: 15px;
  float: left;
}

.wather-container {
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wather-slider {
  width: 100%;
  height: 8px;
  -webkit-appearance: none;
  background: #ddd;
  outline: none;
  border-radius: 5px;
  transition: background 0.3s ease;
}

.wather-slider::-webkit-slider-runnable-track {
  background: #ddd;
  height: 8px;
  border-radius: 5px;
}

.wather-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 30px;
  height: 30px;
  background-size: 30px 30px;

  background-image: url(https://bellayujin.github.io/toDoList/image/drink_0.png);
  border-radius: 50%;
  cursor: pointer;
  margin-top: -10px;
}

.wather-slider::-moz-wather-track {
  background: #ddd;
  height: 8px;
  border-radius: 5px;
}

.wather-slider::-moz-wather-thumb {
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
  background-image: url(https://bellayujin.github.io/toDoList/image/drink_0.png);
  border-radius: 50%;
  cursor: pointer;
}

.wather-labels {
  margin-top: 10px;
}

.water-img {
  display: flex;
  flex-direction: row;
}

.water-img img {
  width: 30px;
}
.checked {
  text-decoration-line: line-through;
}

.todoListCard {
  width: 330px;
  min-height: 300px;
  padding: 30px 30px;
  background-color: rgba(14, 14, 14, 0.7);
  border-radius: 15px;
  color: #fff;
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}

.todoInput {
  width: 100%;
  height: 40px;
  font-size: 18px;
  background-color: transparent;
  box-sizing: border-box;
  border: none;
  text-align: left;
  padding: 5px 10px;
  color: #fff;
  font-family: "Times New Roman", Times, serif;
}

.time-container {
  text-align: right;
  padding: 20px;
  color: white;
  font-family: Arial, sans-serif;
  position: fixed;
  top: 7%;
  right: 8%;
}

.time {
  font-size: 64px;
  font-weight: bold;
  margin-bottom: 10px;
}

.temperature-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
  font-size: 14px;
}

.temperature-info span {
  display: block;
}
